}

.show {
  -ms-flex: auto;
  flex: auto;

  width: 30rem;
  height: 30rem;
  overflow: hidden;
  position: relative;

  margin-right: 0.5rem;
  margin-bottom: 0.5rem;

  -o-transition: 0.25s;
  -webkit-transition: 0.25s;
  transition: 0.25s;
}
.show:hover {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
  z-index: 100;
}

.show img {
  position: absolute;
  left: -1000%;
  right: -1000%;
  top: -1000%;
  bottom: -1000%;
  margin: auto;
  min-height: 100%;
  min-width: 100%;
  width: 50%;
